    height: 500px;
  }
  #speedLine0 {
    animation: line-animation1 0.4s 8 ease-in-out;
  }
  #speedLine1 {
    animation: line-animation2 0.4s 8 ease-in-out;
  }
  #speedLine2 {
    animation: line-animation3 0.4s 8 ease-in-out;
  }
  #speedLine3 {
    animation: line-animation1 0.4s 8 ease-in-out;
  }
  #speedLine4 {
    animation: line-animation1 0.7s 8 ease-in-out;
  }
  #speedLine5 {
    animation: line-animation1 0.4s 8 ease-in-out;
  }
  #speedLine6 {
    animation: line-animation1 0.5s 8 ease-in-out;
  }
  @keyframes flyUp {
    2%  {transform: translateY(0px)}
    100% {
      transform: translateY(-900px);
    }
  }
  @keyframes dance {
  0% {transform: translateY(0px);
  }
  100% {transform: translateY(4px);
  }
  }
  @keyframes rotate {
  100% {transform: rotate(360deg);
  }
  }
  @keyframes scale {
  0% {transform: scale(1,1);
  }
  50% {transform: scale(1.2,1.2);
  }
  100% {transform: scale(1,1);
  }
  }
  @keyframes scale2 {
  0% {transform: scale(1,1);
  }
  50% {transform: scale(1.2,1.2);
  }
  100% {transform: scale(1,1);
  }
  }
  @keyframes scale3 {
  0% {transform: scale(1,1);
  }
  60% {transform: scale(1.3,1.3);
  }
  100% {transform: scale(1,1);
  }
  }
  @keyframes line-animation1 {
  0%    {stroke-dasharray: 5%,90%;}
    50%    {stroke-dasharray: 20%,100%;}
    100%    {stroke-dasharray: 5%,200%;
            stroke-dashoffset: -80%;}
   }
  @keyframes line-animation2 {
  0%    {stroke-dasharray: 5%,95%;}
    50%    {stroke-dasharray: 20%,95%;}
    100%    {stroke-dasharray: 5%,100%;
            stroke-dashoffset: -74%;}
   }
  @keyframes line-animation3 {
  0%    {stroke-dasharray: 5%,41%;}
    50%    {stroke-dasharray: 20%,50%;}
    100%    {stroke-dasharray: 5%,80%;
            stroke-dashoffset: -30%;}
   }
  @keyframes opacity {
    0%   { opacity: 0; }
    100% { opacity: 1; }
   }
   @keyframes growDot {
     0% {transform: scale(0,0);opacity: 0;
    }
    30% {
      opacity: 0;
    }
     32% {
       opacity: 1;
       transform: scale(0.2,0.2);
       z-index: 1
     }
     35%{
       transform: scale(0.4,0.4);
     }
     90% {
       transform: scale(600,600);
       z-index: 1
     }
   }
   @media (max-width: 1024px) {
     .space-guy__container {
       width:100vw;
       height:100vh;
       margin: 0;
       padding: 0;
     }
   }
</style>



